<template>
  <div style="background-color: var(--dash-card-color); height:100vh">
    <mobile-header title="상조 추가" class="mb-0" @close="backRouting" />
    <div id="m_admin-main__page" class="pt-0">
      <section class="corp-change" style="padding:18px 16px; ">
        <div class="step-desc" style="padding-top:104px">
          <p>
            상조 정보를 등록해 주세요
          </p>
        </div>

        <div id="m_join" class="px-0">
          <corp-list
            v-if="!corp"
            class="corp-list__mobile"
            :corp="corp"
            @selectCorp="selectCorp"
          />
          <section v-if="!notBelongCorp" class="id-pw__wraps">
            <div v-if="corp">
              <div class="corp-input__wrap">
                <h4>{{ corp.text }}</h4>
                <div class="certify">
                  <h4 class="mb-0 mt-4">인증 비밀번호</h4>
                  <v-text-field
                    v-model="corpPassword"
                    hide-details
                    dense
                    placeholder="초기 비밀번호 0000"
                    :append-icon="
                      show2 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'
                    "
                    :type="show2 ? 'text' : 'password'"
                    color="main"
                    @click:append="show2 = !show2"
                  />
                </div>
                <div class="button__wraps" style="text-align:right">
                  <button @click="certifyCorp">인증하기</button>
                </div>
              </div>

              <div class="retry-button__wraps mt-4">
                <button @click="retry">다시 선택하기</button>
              </div>
            </div>
          </section>

          <p class="corp-input-notice">
            상조에 따라 본 서비스 정책의 적용범위가 달라질 수 있습니다.
          </p>
        </div>
      </section>
    </div>

    <button
      class="corp-change__button"
      style="width: 90%; position: absolute; bottom: 32px; margin:auto;left: 20px;right: 20px;"
      @click="addCorp"
    >
      소속상조 추가하기
    </button>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import MobileHeader from "@/components/common/MobileHeader.vue";
import CorpChangeModal from "@/components/common/CorpChangeModal.vue";
import CorpList from "../../components/common/CorpList.vue";
import myMixin from "../../mixin/mixin";

export default {
  components: {
    MobileHeader,
    CorpChangeModal,
    CorpList
  },
  mixins: [myMixin],

  data() {
    return {
      changeFlag: false,
      corpName: null,
      corp: null,
      corpPassword: null,
      notBelongCorp: false,
      certifyNumber: "",
      pwCheckFlag: false,
      show2: false
    };
  },

  computed: {
    ...mapGetters(["memberInfo"])
  },
  methods: {
    backRouting() {
      this.$router.go(-1);
    },

    move(path) {
      this.$router.push(path);
    },

    addCorp() {
      if (!this.pwCheckFlag) {
        alert("상조 인증을 먼저 진행해주십시오");
        return;
      }
      alert("서비스 준비중 입니다");
      this.backRouting();
      return null;
    },

    selectCorp(corp) {
      this.searchCorp = null;
      this.corp = corp;
    },
    retry() {
      this.corp = null;
      this.pwCheckFlag = false;
    },

    certifyCorp() {
      this.pwCheckFlag = false;

      for (let i = 0; i < this.getCodeList("CORP").length; i++) {
        if (
          this.getCodeList("CORP")[i].value === this.memberInfo.memberCorpCode
        ) {
          if (this.getCodeList("CORP")[i].codeSubText === this.corpPassword) {
            this.pwCheckFlag = true;
            alert("인증 되었습니다");
          }
        }
      }
      if (!this.pwCheckFlag) {
        return alert("상조회사 비밀번호를 확인해 주세요.");
      }
    }
  }
};
</script>

