<template>
  <div class="corp-list__pc">
    <v-text-field
      v-model="searchCorp"
      placeholder="상조 이름을 검색해주세요"
      hide-details
      dense
      color="main"
    >
    </v-text-field>

    <div class="corp-list__wrap">
      <div v-if="noMatchCorp(getCodeList('CORP'))" class="no-match__wrap">
        <p class="my-0">등록된 상조 정보가 없습니다</p>
        <p class="my-0 mt-2 mb-7">고객센터에 상조 등록을 문의해주세요</p>
        <div class="call mb-6">
          <div class="img__wraps">
            <img src="@/assets/icons/phone.svg" alt="" width="16px" />
          </div>
          <p class="ma-0 pl-4">상조 등록 문의하기</p>
        </div>
      </div>
      <template v-for="(corp, index) in getCodeList('CORP')">
        <div v-if="searchFlag || matchCorp(corp.text)" :key="index">
          <span @click="selectCorp(corp)"> {{ corp.text }}</span>
          <button @click="selectCorp(corp)">선택</button>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import myMixin from "@/mixin/mixin";
export default {
  mixins: [myMixin],
  props: {},
  data() {
    return {
      searchCorp: null
    };
  },

  computed: {
    searchFlag() {
      if (!this.searchCorp) return true;

      return false;
    }
  },
  methods: {
    matchCorp(corp) {
      if (corp.toUpperCase().includes(this.searchCorp)) {
        return true;
      } else {
        return false;
      }
    },

    noMatchCorp(codeList) {
      if (!codeList || !this.searchCorp) {
        return false;
      }

      const textList = codeList.map(v => v.text);
      const matchList = [];

      textList.forEach(v => {
        if (v.toUpperCase().includes(this.searchCorp)) {
          matchList.push(v);
        }
      });

      if (!matchList.length) return true;

      return false;
    },
    selectCorp(corp) {
      this.searchCorp = null;

      this.$emit("selectCorp", corp);
    }
  }
};
</script>

<style></style>
